import trailerStars from "../../components/trailerStars.vue"
import aline from "../../components/aline.vue"
export default {
	data() {
		return {
			trailerInfo:{},   //详情
			plotPicsArray:[], //剧照列表
			directorArray:[], //导演列表
			actorArray:[]     //演员列表
		}
	},
	//监听页面初次渲染完成
	onReady() {
		this.videoContext = uni.createVideoContext('myTrailer')
	},
	onHide() {
		//页面隐藏时暂停播放
		this.videoContext.pause()
	},
	onShow() {
		//页面显示时播放
		if(this.videoContext){
			this.videoContext.play()
		}
	},
	onLoad(params) {
		var _this = this;
		var trailerId = params.trailerId
		uni.setNavigationBarColor({
			frontColor:"#ffffff",
			backgroundColor:"#000000"
		})
		//请求影片详情
		uni.request({
			url: _this.serverURL + '/search/trailer/'+ trailerId, 
			method:"POST",
			success: (res) => {
				if(res.data.status == 200){
					_this.trailerInfo = res.data.data[0];
					_this.plotPicsArray = _this.trailerInfo.plotPics;
				}
			}
		});
		//获取导演信息
		uni.request({
			url: _this.serverURL + '/search/staff/'+ trailerId + '/1' , 
			method:"POST",
			success: (res) => {
				if(res.data.status == 200){
					_this.directorArray = res.data.data;
				}
			}
		});
		//获取演员信息
		uni.request({
			url: _this.serverURL + '/search/staff/'+ trailerId + '/2' , 
			method:"POST",
			success: (res) => {
				if(res.data.status == 200){
					_this.actorArray = res.data.data;
				}
			}
		});
	},
	//此函数仅支持小程序端
	onShareAppMessage(res) {
		return{
			title:this.trailerInfo.name,
			path:'/pages/movie/movie?trailerId='+this.trailerInfo.id
		}
	},
	//监听原生标题栏按钮点击事件 
	onNavigationBarButtonTap(e) {
		var _this = this;
		var index = e.index
		var trailerInfo = _this.trailerInfo
		var trailerId = trailerInfo.id
		var trailerName = trailerInfo.name
		var cover = trailerInfo.cover
		var poster = trailerInfo.poster
		
		if(index == 0){
			uni.share({
				provider:"weixin",
				scene: "WXSceneSession",
				type: 0,
				href:"http://localhost:8080/#/pages/movie/movie?trailerId="+trailerId,
				title:"《"+trailerName+"》",
				summary: "《"+trailerName+"》",
				imageUrl:cover,
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				}
			})
		}
	},
	methods: {
		lookMe(e){
			var _this = this;
			var imgIndex = e.currentTarget.dataset.imgindex 
			uni.previewImage({
				current:_this.plotPicsArray[imgIndex],
				urls:_this.plotPicsArray
			})
		},
		lookStaffs(e){
			var _this = this;
			var staffIndex = e.currentTarget.dataset.staffindex 
			//拼接导演和演员数组
			var newStaffArray = []
			newStaffArray = newStaffArray.concat(_this.directorArray).concat(_this.actorArray)
			var urls = []
			for (let i = 0; i < newStaffArray.length; i++) {
				urls.push(newStaffArray[i].photo)
			}
			uni.previewImage({
				current:urls[staffIndex],
				urls:urls
			})
			
		}
	},
	components:{
		trailerStars,
		aline
	}
}
